export default {
    middleware: 'auth',
    auth: 'guest',
    layout: 'none',
    
    data () {
        return {
            email: '',
            password: ''
        }
    },

    methods: {
        async onLogIn () {
            try {

                let response = await this.$auth.loginWith('local', {
                    data: {
                        email: this.email,
                        password: this.password
                    }
                })
                await this.$store.dispatch('setLoggedUser')
                
                this.$router.push('/')

            } catch (error) {
                console.log(error)
            }
        }
    }
}
